.games {
    margin-bottom: 100px;
}

.games__loader {
    display: flex;
    align-items: center;
    justify-content: center;
}

.game-header {
    text-align: center;
    margin-bottom: 20px;
}

.game-title {
    font-size: 70px;
    color: #fff;
