.game-row {
    
}

@media (max-width: 768px) {
    .game-row__cell {
        colspan: 3;      
    }
}

.game-state {
    font-weight: 700;
    font-size: 20px;
    text-transform: uppercase;
}

.game-state--win {
    background: linear-gradient(90deg, #D9ABE7 1.82%, #65BAF9 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
}

.game-state--lose {
    color: #9199B0;
}

.game-row__persona {
    display: flex;
    align-items: center;
    gap: 20px;
    font-size: 16px;
}

.game-row__persona img {
    width: 40px;
    height: 40px;
    border: 2px solid #fff;
    border-radius: 50%;
}