.game-banner {
    padding: 60px 20px;
    border-radius: 32px;
    color: #fff;
    font-size: 28px;
    background: #10141E;
}

.game-banner + .game-banner {
    margin-top: 100px;
}

.game-banner__header {
    margin-bottom: 20px;
    text-align: center;
    font-size: 30px;
    text-transform: uppercase;
    font-weight: bold;
}

.game-banner__inner {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 50px;
}

.game-banner__opponent {
    position: relative;
    text-align: center;
}

.game-banner__opponent-inner {
    position: relative;
    margin-bottom: 20px;
    height: 235px;
    width: 235px;
    text-align: center;
    border-radius: 50%;
    border: 5px solid #fff;
    background: rgba(255,255,255,0.5);
}

.game-banner__opponent-name {
    font-size: 26px;
    font-weight: bold;
    margin-bottom: 20px;
}

.game-banner__opponent img {
    max-width: 100%;
    max-height: 100%;
    border-radius: 50%;
}

.game-banner__vs {
    width: 65px;
    height: 65px;
    font-size: 32px;
    font-weight: bold;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #25304E;
}

.game-info {
    background: #1B2131;
    font-size: 24px;
    max-width: 876px;
    margin: 40px auto;
    border-radius: 16px;
}

.game-info-item + .game-info-item {
    border-top: 2px solid #10141E;
}

.game-info-item {
    padding: 24px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}



.game-banner__countdown {
    text-align: center;
    margin: 20px auto;
    margin-bottom: 40px;
}

.game-banner__cta {
    display: flex;
    align-items: center;
    gap: 10px;
    justify-content: center;
}

.game-banner__cta button {
    min-width: 200px;
}

.game-state-label {
    font-size: 30px;
}

.game-balance {
    position: relative;
    display: flex;
    gap: 5px;
    align-items: center;
    margin: 15px auto 50px;
    max-width: 720px;
}

.game-balance__bars {
    width: 100%;
    display: flex;
    flex: 1;
}

.game-balance__bar {
    height: 40px;
}

.game-balance__boss {
    padding-left: 20px;
    background: #D5ACE8;
    border-radius: 16px 0px 0px 16px;
}

.game-balance__dog {
    text-align: right;
    padding-right: 20px;
    background: #79B8F7;
    border-radius: 0px 16px 16px 0px;
}

.game-info-item__label {
    display: flex;
    align-items: center;
    gap: 10px;
}